<template>
  <v-card elevation="2" outlined>
    <v-card-text>
      <p class='text-h6 text--primary'> RF Status</p>

      <v-container>
        <v-row>        
          <v-col align-self="center" cols="2" class='text-body-1 d-flex align-center justify-end font-italic'>Temperature</v-col>
          <v-col align-self="center" cols="3" class='text-body-1 d-flex align-center font-weight-bold'> {{parseFloat(Status.Temperature).toFixed(2)}} &#176;C </v-col>

          <v-col align-self="center" cols="2" class='text-body-1 d-flex align-center justify-end font-italic'>Fan</v-col>
          <v-col align-self="center" cols="3" class='text-body-1 d-flex align-center font-weight-bold'> {{Status.FanSpeed}} RPM </v-col>
        </v-row>

        <v-row>
          <v-col align-self="center" cols="2" class='text-body-1 d-flex align-center justify-end font-italic'>Software Mute</v-col>
          
          <v-col align-self="center" cols="3">
            <v-btn-toggle color="primary" mandatory v-model="new_SwEnable">
              <v-btn outlined :value="true" :color="new_SwEnable? 'green' : ''"> 
                Unmute
                <v-icon dark right :color="new_SwEnable? 'green' : ''"> mdi-checkbox-marked-circle </v-icon>
              </v-btn>
              <v-btn outlined :value="false" :color="new_SwEnable == false? 'amber' : ''">
                Mute 
                <v-icon dark right :color="new_SwEnable == false? 'amber' : ''"> mdi-cancel </v-icon>
              </v-btn>
            </v-btn-toggle>
          </v-col>

          <v-col align-self="center" cols="2" class='text-body-1 d-flex align-center justify-end font-italic'>Discrete Mute</v-col>
          <v-col align-self="center" cols="3" class='text-body-1 d-flex align-center font-weight-bold green--text'> Unmuted </v-col>
        </v-row>

        <v-row>
          <v-col align-self="center" cols="2" class='text-body-1 d-flex align-center justify-end font-italic'>Step Attenuator</v-col>
          <v-col align-self="center" cols="2" class="text-body-1 font-weight-medium">
            <v-text-field type="number" suffix="dB" dense hide-details
              :step="UnitOption.Tx.StepAtten.Rez" :min="UnitOption.Tx.StepAtten.Min" :max="UnitOption.Tx.StepAtten.Max"
              :rules="[StepAttenIsValid(new_StepAtten)]"
              :label="StepAttenIncrementString" 
              v-model.number="new_StepAtten"> </v-text-field>
          </v-col>
          <v-col cols="1"></v-col>

          <v-col align-self="center" cols="2" class='text-body-1 d-flex align-center justify-end font-italic'>RF Offset</v-col>
          <v-col cols="2" align-self="center" class="text-body-1 font-weight-medium">
            <v-text-field suffix="dB" dense hide-details
              v-model.number="new_RfOffset"
              type="number" label="RF Offset"
              step="0.10"
              > </v-text-field>
          </v-col>
        </v-row>

        <v-row>
          <v-col cols="2" class='text-body-1 d-flex align-center justify-end font-italic'> Power </v-col>
          <v-col cols="2" align-self="center" class='text-body-1 d-flex align-center font-weight-bold'> 
            {{GetRfPowerLabel}}
          </v-col>

          <v-col cols="2" align-self="center" class="text-body-1 font-weight-medium"> 
            <v-text-field suffix="dBm" dense hide-details
              v-model.number="new_DesiredOutputPower"
              type="number" label="Desired Power"
              step="0.10"
              > </v-text-field>
          </v-col>

          <v-col cols="2" class="text-body-1 d-flex">
            <v-btn class="mx-1">Set Desired Power </v-btn>
          </v-col>
        </v-row>

        <v-row>
          <v-col cols="2" class='text-body-1 d-flex align-center justify-end font-italic'> Reflected Power </v-col>
          <v-col cols="2" align-self="center" class='text-body-1 d-flex align-center font-weight-bold'> 
            {{parseFloat(ReflectiveSettings.Power).toFixed(2)}} dBm 
          </v-col>

          <v-col cols="2" align-self="center" class="text-body-1 font-weight-medium">
            <v-text-field suffix="dBm" dense hide-details
              v-model.number="new_MaxReflectivePower"
              type="number" label="Max Reflected Power"
              step="0.10"
              > </v-text-field>
          </v-col>

          <v-col cols="2" class="text-body-1 d-flex">
            <v-btn class="mx-1">Set Reflected power </v-btn>
          </v-col>
        </v-row>

        
      </v-container>

    </v-card-text>

  </v-card>
</template>


<script>
import _ from 'lodash';

import { mapActions } from 'vuex';

const AlcStatuses = ["Active", "Inactive", "Warning Output High", "Warning Output Low", "Fault Output High", "Fault Output Low"];

const FaultOptions = [
  {type: "None", background: "", textColor: ""},
  {type: "Alarm", background: "amber", textColor: "amber--text"}, 
  {type: "Fault", background: "red", textColor: "red--text"}];


export default {
  name: 'Status',

  props: {

    Status: {
      type: Object,
      default: function() {
        return {
          TxPower: 38.5,
          SwTxEnabled: true,
          TxRefLocked: true,
          TxStepAtten: 25.5,

          Temperature: 55.4,

          FanSpeed: 3490,
          
          Faults: ['Master', 'Drain Voltage', 'Fan Power'],
        }
      }
    },

    ForwardPower: {
      type: Object,
      default: function() {
        return {
          TxPower: 38.5,
          TxStepAtten: 25.5,

          FaultType: 2,
          MinPower: 20.0,
          MaxPower: 42.0,
        }
      }
    },


    UnitOption: {
      type: Object,
      default: function() {
        return {
          Tx: {
            StepAtten: {
              Min: 0.0,
              Max: 31.75,
              Rez: 0.25
            },

            LoFreq: {
              PresetEnabled: true,
              RangeEnabled: false,
              Presets: [28.05, 29.00]
            },

            Alc: {
              Enabled: false,
              Active: false,
            }
          }
        }
      }
    },

    Alias: {
      type: String,
      default: null
    },

    RfOffset: {
      type: Number,
      default: 3.7
    },

    ReflectiveSettings: {
      type: Object,
      default: function() {
        return {
          Selection: 2,
          Power: 28.0,
          Max: 39.0,
        }
      }
    },

  },

  computed: {
    GetRfPowerLabel() {
      switch (this.PowerConversion) {
        // dBw
        case 1: 
          return parseFloat(this.GetComputedPower - 30.0).toFixed(2) + " dBW";

        // Watt
        case 2:
          return parseFloat(this.dBmToWatt(this.GetComputedPower)).toFixed(2) + " W";

        // dBm
        default:
          return parseFloat(this.GetComputedPower).toFixed(2) + " dBm";
      }
    },

    GetComputedPower() {
      return this.Status.TxPower + this.RfOffset;
    },

    StepAttenIncrementString() {
      return "Step Increment is " + parseFloat(this.UnitOption.Tx.StepAtten.Rez).toFixed(2) + "dB";
    },
  },

  methods: {
    ...mapActions([
      "PerformSetStepAtten", 
      "PerformSetSwTxEnabled",
      "PerformSetPowerOnState"
    ]),

    dBmToWatt(power) {
      return (1 * Math.pow(10, power / 10)) / 1000.0;
    },

    toggleAlcStatus() {
      this.alcStatus = (this.alcStatus + 1) % AlcStatuses.length;
    },

    
    debounceMethod: _.debounce(function(method) {
      method();
    }, 1000),

    ChangePowerConversion() {
      this.PerformSetPowerConversion(this.new_PowerConversion);
    },

    ChangePowerOnState() {
      this.PerformSetPowerOnState(this.new_BootupRf);
    },

    ChangeStepAtten() {
      this.PerformSetStepAtten(this.new_StepAtten);
    },

    ChangeSwTxEnable() {
      this.PerformSetSwTxEnabled(this.new_SwEnable);
    },

    StepAttenIsValid(atten) {
      return atten >= this.UnitOption.Tx.StepAtten.Min
        && atten <= this.UnitOption.Tx.StepAtten.Max
        && atten % this.UnitOption.Tx.StepAtten.Rez == 0;
    },
  },

  created() {
    this.FaultOptions = FaultOptions;
  },

  data() {
    return {
      
      new_SwEnable: this.Status.SwTxEnabled,
      
      new_DesiredOutputPower: 0,
      new_RfOffset: this.RfOffset,
      new_StepAtten : this.Status.TxStepAtten,

      new_MaxReflectivePower: this.ReflectiveSettings.Max,
    }
  },

  watch: {
    new_SwEnable: function() {
      if (this.new_SwEnable != this.SwTxEnabled) {
        this.debounceMethod(this.ChangeSwTxEnable);
      }
    },

    new_StepAtten: function() {
      if (this.new_StepAtten != this.StepAtten) {
        this.debounceMethod(this.ChangeStepAtten);
      }
    },

    new_PowerConversion: function() {
      if (this.new_PowerConversion != this.PowerConversion) {
        this.debounceMethod(this.ChangePowerConversion);
      }
    },

    new_BootupRf: function() {
      if (this.new_BootupRf != this.PowerOnState) {
        this.debounceMethod(this.ChangePowerOnState);
      }
    }
  }
  
}
</script>

<style scoped>
.text-field{
  border: none !important;
  background: none !important;
  padding: 0px !important;
  color: rgba(0, 0, 0, 0.6) !important;
}
</style>
