<template>
  <div>
    <v-card>
      <v-toolbar flat :color="GetBannerColor" dark>
        <v-toolbar-title>Mission Microwave</v-toolbar-title>
         <v-spacer/>
         <div class='headline d-flex align-center font-weight-bold' v-if="getUnitAlias.length > 0"> {{getUnitAlias}} </div>
         <v-spacer/>
         <v-btn color="white" class="primary--text" v-on:click="SigningOut">
           Log Out
         </v-btn>
      </v-toolbar>

      <v-tabs vertical class="my-tabs" v-model="NewTabSelected">
        <v-tab v-for="tabInfo in TabHeaders" :key="tabInfo.name" class="flex-start">
          <v-icon left class='text-h4'> {{tabInfo.icon}} </v-icon>
          {{tabInfo.name}}
        </v-tab>

        <!-- Status -->
        <v-tab-item>
          <status 
            v-bind:Status="getUnitStatus" 
            v-bind:RfOffset="getRfOffset" 
            v-bind:PowerConversion="getPowerConversion"></status>

          <!-- <rf-power> </rf-power> -->
          <!-- <reflective-power> </reflective-power> -->
          <alc-settings></alc-settings>
        </v-tab-item>

        <!-- Event Page -->
        <v-tab-item>
          <mission-event></mission-event>
        </v-tab-item>

        <!-- Logs -->
        <v-tab-item>
          <monitor></monitor>
        </v-tab-item>

        <!-- Settings -->
        <v-tab-item>
          <unit-info 
            v-bind:Info="getUnitInfo" 
            v-bind:Status="getUnitStatus"
            v-bind:Alias="getUnitAlias"
            v-bind:RfOffset="getRfOffset"
            v-bind:PowerConversion="getPowerConversion"
            v-bind:PowerOnState="getPowerOnState"></unit-info>
          <rf-settings></rf-settings>
          <clock-ntp v-bind="getClockNtp"></clock-ntp>
          <ip-configuration v-bind="getIpInfo"></ip-configuration>
          <serial-interface v-bind="getSerialOptions"></serial-interface>
          <update-firmware></update-firmware>
          <test-fault></test-fault>
        </v-tab-item>

        <v-tab-item>
          <snmp> </snmp>
        </v-tab-item>

      </v-tabs>

    </v-card>
  </div>
</template>


<script>
// import websocketPlugin from '../store/plugins/web-socket'

import { mapGetters, mapActions } from 'vuex';

import Status from './Status.vue'
// import ReflectivePower from './ReflectivePower.vue'
import AlcSettings from './AlcSettings.vue'
import ClockNtp from './ClockNtp.vue'
import IpConfiguration from './IpConfiguration.vue'
import MissionEvent from './MissionEvent.vue'
import Monitor from './Monitor.vue'
import SerialInterface from './SerialInterface.vue'
import UpdateFirmware from './UpdateFirmware.vue'
import RfSettings from './RfSettings.vue'
import UnitInfo from './UnitInfo.vue'
import TestFault from './TestFault.vue'
import Snmp from './Snmp.vue'

const TabHeaders = [
  {
    name: "RF Status",
    icon: "mdi-view-dashboard-outline"
  },
  {
    name: "Event",
    icon: "mdi-calendar-blank-outline"
  },
  {
    name: "Plots",
    icon: "mdi-book-open-outline"
  },
  {
    name: "Settings",
    icon: "mdi-cog"
  },
  {
    name: "SNMP",
    icon: "mdi-lan"
  }
];

export default {
  name: 'MissionTab',

  components: {
    Status,
    AlcSettings,

    IpConfiguration,
    ClockNtp,
    MissionEvent,
    Monitor,
    SerialInterface,
    UpdateFirmware,
    RfSettings,
    UnitInfo,
    TestFault,
    Snmp
  },

  computed: {
    ...mapGetters([
      "getUnitInfo",
      "getIpInfo", 
      "getUnitStatus", 
      "getSerialOptions",  
      "getClockNtp",
      "getUnitAlias",
      "getRfOffset",
      "getPowerConversion",
      "getPowerOnState"]),

    GetBannerColor() {
      var status = this.getUnitStatus;

      if (status.Faults != null 
        && status.Faults.length == 0)
      {
        if (status.SwTxEnabled) {
          return 'green';
        }
        return 'amber';
      }
      return 'red';
    }
  },

  props: {
    UnitAlias: {
      type: String,
      default: "Javelin 2"
    }
  },

  data: function() {
    return {
      NewTabSelected: 0
    };
  },

  methods: {
    ...mapActions(["PerformLogOut"]),

    SigningOut: function() {
      sessionStorage.removeItem('credential');
      this.$router.push('/');
      this.PerformLogOut();

      const url = 'http://192.168.235.56:28080/logout';
      fetch(url, {
        method: 'POST',
        body: JSON.stringify({credential: "C0FFEE"})
      });
    }
  },

  created: function() {
    this.TabHeaders = TabHeaders;

    // websocketPlugin(this.$store);
  }

}
</script>

<style>
div.my-tabs [role="tab"] {
  justify-content: flex-start;
}

</style>