<template>
  <div>
    <v-card elevation="2" outlined>
      <v-card-text>
        <p class="text-h6 text--primary">
          Fault Definitions
        </p>
        <v-container>
          <v-row v-for="(row, i) in RowData" :key="i">
            <v-col v-for="(item, index) in row.Column" :key="index">
              <v-row>
                <v-col cols="4" align-self="center" class="text-body-1 d-flex justify-end font-italic"> {{item.name}} </v-col>
                <v-col cols="2" align-self="center"> 
                  <v-btn-toggle mandatory color="primary">
                    <div v-for="(fault, j) in item.faultOptions" :key="j">
                      <v-btn outlined :value="fault" :disabled="fault != item.faultType" color="redk">
                        {{fault}}
                      </v-btn>
                    </div>
                  </v-btn-toggle>
                </v-col>
              </v-row>
            </v-col>
          </v-row>

          <v-row>
            <v-col cols="2" class='text-body-1 d-flex align-center justify-end font-italic'> High RF Power </v-col>
            <v-col cols="3">
              <v-btn-toggle class="ma-2" mandatory color="primary" v-model="new_HighRfFaultOption">
                <div v-for="(option, i) in FaultOptions" :key="i">
                  <v-btn outlined :value="i" :class="option.textColor" :color="option.background">
                    {{option.type}}
                  </v-btn>
                </div>
              </v-btn-toggle>
            </v-col>
            <v-col v-if="new_HighRfFaultOption != 0" cols="3" align-self="center" class="text-body-1 font-weight-medium">
              <v-text-field suffix="dBm" dense hide-details
                v-model.number="new_MaxOutputPower"
                type="number" label="High RF Power"
                step="0.10"
                > </v-text-field>
            </v-col>
          </v-row>

          <v-row>
            <v-col cols="2" class='text-body-1 d-flex align-center justify-end font-italic'> Low RF Power </v-col>
            <v-col cols="3">
              <v-btn-toggle class="ma-2" mandatory color="primary" v-model="new_LowRfFaultOption">
                <div v-for="(option, i) in FaultOptions" :key="i">
                  <v-btn outlined :value="i" :class="option.textColor" :color="option.background">
                    {{option.type}}
                  </v-btn>
                </div>
              </v-btn-toggle>
            </v-col>
            <v-col v-if="new_LowRfFaultOption != 0" cols="3" align-self="center" class="text-body-1 font-weight-medium">
              <v-text-field suffix="dBm" dense hide-details
                v-model.number="new_MinOutputPower"
                type="number" label="Low RF Power"
                step="0.10"
                > </v-text-field>
            </v-col>
          </v-row>

          <v-row>
            <v-col cols="2" class='text-body-1 d-flex align-center justify-end font-italic'> High Reflected Power </v-col>
            <v-col cols="3">
              <v-btn-toggle class="ma-2" mandatory color="primary" v-model="new_HighReflectedOption">
                <div v-for="(option, i) in FaultOptions" :key="i">
                  <v-btn outlined :value="i" :class="option.textColor" :color="option.background">
                    {{option.type}}
                  </v-btn>
                </div>
              </v-btn-toggle>
            </v-col>
            <v-col v-if="new_HighReflectedOption != 0" cols="3" align-self="center" class="text-body-1 font-weight-medium">
              <v-text-field suffix="dBm" dense hide-details
                v-model.number="new_MaxReflectedPower"
                type="number" label="High Reflected Power"
                step="0.10"
                > </v-text-field>
            </v-col>
          </v-row>
        </v-container>
      </v-card-text>
    </v-card>

    <v-card elevation="2" outlined>
      <v-card-text>
        <p class="text-h6 text--primary">
          Inhibit

          <v-btn-toggle mandatory color="primary" class="ma-2">
            <v-btn outlined class="primary--text">
              Closed
            </v-btn>
            <v-btn outlined disabled class="primary--text">
              Open
            </v-btn>
          </v-btn-toggle>
        </p>
        
      </v-card-text>
    </v-card>

  </div>  
</template>


<script>

const AllRows = [
  {
    Column: [
    {
      name: "Overdriven",
      faultType: "Fault",
      faultOptions: ["None", "Fault"]
    },
    {
      name: "Temperature Fault",
      faultType: "Fault",
      faultOptions: ["None", "Fault"]
    }],
  }
];

const FaultOptions = [
  {type: "None", background: "", textColor: ""},
  {type: "Alarm", background: "amber", textColor: "amber--text"},
  {type: "Fault", background: "red", textColor: "red--text"}];

export default {
  name: 'RfSettings',
  
  props: {
  },

  created() {
    this.RowData = AllRows;
    this.FaultOptions = FaultOptions;
  },

  computed: {
  },

  methods: {
  },

  watch: {
  },

  data() {
    return {
      new_HighRfFaultOption: 2,
      new_LowRfFaultOption: 2,
      new_HighReflectedOption: 2,

      new_MaxOutputPower: 41.2,
      new_MinOutputPower: 21.5,

      new_MaxReflectedPower: 35.6
    }
  }


}
</script>

<style scoped>
.input-field {
  padding: 5px;
  margin: 5px;
}
</style>